(function() {
    var menuHeading = document.getElementsByClassName('menu_heading')[0];
    var menuContent = document.getElementsByClassName('menu_content')[0];

    menuHeading.addEventListener('click', function() {
        if (this.className.indexOf('active') != -1) {
            this.className = 'menu_heading';
        } else {
            this.className = 'active menu_heading';
        }
    }, false);

    menuHeading.addEventListener('mouseenter', function() {
        this.className = 'active menu_heading';
    }, false);

    menuContent.addEventListener('mouseleave', function() {
        menuHeading.className = 'menu_heading';
    }, false);

})();